export const query = graphql`
    query MyQuery {
        file(relativePath: { eq: "moneysprout--transparent.png" }) {
            childImageSharp {
                fluid(quality: 100) {
                    ...GatsbyImageSharpFluid
                }
            }
        }
    }
`
